import React from 'react';
import ImageAvatars from './ImageAvatars';
import LetterAvatars from './LetterAvatars';
import IconAvatars from './IconAvatars';

const Avatars = () => {
  return (
    <div>
      <h1>Avatars</h1>
      <h2>Image Avatars </h2>
      <ImageAvatars />
      <h2>Letter Avatars </h2>
      <LetterAvatars />
      <h2>Icon Avatars </h2>
      <IconAvatars />
    </div>
  )
}

export default Avatars;