import React from 'react';
import ButtonAppBar from './ButtonAppBar';
import SimpleAppBar from './SimpleAppBar';

const AppBar = () => {
  return (
    <div>
      <h1>App Bar</h1>
      <h2>ButtonAppBar </h2>
      <ButtonAppBar />
      <h2>SimpleAppBar </h2>
      <SimpleAppBar />
    </div>
  )
}

export default AppBar;