import React from 'react';
import SimpleChips from './SimpleChips';
import OutlinedChips from './OutlinedChips';
import ChipsArray from './ChipsArray';

function Chips(){
  return (
    <div>
      <h1>Chips </h1>
      <h2>SimpleChips </h2>
      <SimpleChips />
      <h2>OutlinedChips </h2>
      <OutlinedChips />
      <h2>ChipsArray </h2>
      <ChipsArray />
    </div>
  )
}

export default Chips;