import React from 'react';
import SimpleTabs from './SimpleTabs';
import TabsWrappedLabel from './TabsWrappedLabel';
import DisabledTabs from './DisabledTabs';
import FullWidthTabs from './FullWidthTabs';

function Tabs(){
  return (
    <div>
      <h1>Tabs</h1>
      <h2>SimpleTabs </h2>
      <SimpleTabs />
      <h2>TabsWrappedLabel </h2>
      <TabsWrappedLabel />
      <h2>DisabledTabs </h2>
      <DisabledTabs />
      <h2>FullWidthTabs </h2>
      <FullWidthTabs />
    </div>
  )
}

export default Tabs;