import React from 'react';
import SimpleBadge from './SimpleBadge';
import BadgeMax from './BadgeMax';

const Badges = () => {
  return (
    <div>
      <h1>Badges </h1>
      <h2>SimpleBadge </h2>
      <SimpleBadge />
      <h2>BadgeMax </h2>
      <BadgeMax />
    </div>
  )
}

export default Badges;